<template>
  <zTitle class="wrapper" level="1" text="">
    <zExample title="">
      <commonTable :tableMap="example1.tableMap" :tableData="example1.tableData" :tableConfig="example1.tableConfig"
        @viewDetails="viewDetails" @edit="edit" @selection-change="selectChange" />
      <template v-slot:document>
        <exp1 />
      </template>
    </zExample>
    <zExample title="">
      <h4>slot</h4>
      <p>, </p>
      <commonTable :tableMap="example2.tableMap" :tableData="example2.tableData" @editEject="editEject">
        <template v-slot:btn="{ rowData, template }">
          <el-button v-for="btn in template.btn" :key="btn.label" :type="btn.type || 'primary'"
            :style="rowData.isEdit ? btn.style: {'background':'red'}" :size="btn.size || 'mini'"
            @click="vmSelf[btn.event](rowData)" v-text="getBtnLabel(rowData, btn)" />
        </template>
      </commonTable>
      <template v-slot:document>
        <exp2 />
      </template>
    </zExample>
    <zExample title="">
      <h4>slot</h4>
      <commonTable :tableMap="example3.tableMap" :tableData="example3.tableData" @editEject="editEject">
        <template v-slot:btn="{ template, scope }">
          <template v-for="(item,key) in template">
            <el-input :key="key" v-if="key == 'input'" v-model="example3.input[scope.$index]" placeholder="">
            </el-input>
            <el-select :key="key" v-if="key == 'select'" v-model="example3.value[scope.$index]" placeholder="">
              <el-option v-for="item in example3.options" :key="item.value" :label="item.label" :value="item.value" />
            </el-select>
          </template>
        </template>
      </commonTable>
      <template v-slot:document>
        <exp3 />
      </template>
    </zExample>
		<zExample title="">
		  <h4>slot2</h4>
			<el-row style="margin-top:30px" class="table_his">
				<commonTable :tableMap="example4.tableMap" :tableData="example4.tableData" @editEject="editEject" :tableConfig="example4.tableConfig">
					<template v-slot:btn="{ rowData, template, scope }">
						<template v-for="(item,key) in template">
							<div v-if="key == 'img'" :key="key">
								 <span>{{scope.$index+1}}&emsp;</span>
								<img style="margin-left:20px;" v-if="rowData.modifyNumber" src="@/views/classes/icon/sanjiao.png" @click.stop="selectEditHistorical(scope.$index,rowData)" alt="">
								<div class="historyInvoice" v-show="example4.showKey[scope.$index]" >
									<div v-for="item in example4.LedgerHistory" :key="item.id" >
										<div class="list">
											<span class="date">{{item.updatedAt}}</span>
											<p class="text">{{item.modifyContent}}</p>
										</div>
									</div>
								</div>
							</div>
						</template>
					</template>
				</commonTable>
			</el-row>
		  <template v-slot:document>
				<!-- md -->
				<exp4 />
		  </template>
		</zExample>
    <testMD />
  </zTitle>
</template>
<script>
  import testMD from "@/assets/markdown/components/table/index.md";
  import exp1 from "@/assets/markdown/components/table/exp1.md";
  import exp2 from "@/assets/markdown/components/table/exp2.md";
  import exp3 from "@/assets/markdown/components/table/exp3.md";
  import exp4 from "@/assets/markdown/components/table/exp4.md";
  import commonTable from "@/components/Common/table.vue";

  // 1
  const mixin1 = {
    data: () => {
      return {
        example1
      }
    },
    methods: {
      viewDetails() {
        this.$message.success('')
      },
      edit() {
        this.$message.success('')
      },
      selectChange(val) {
        console.log(val);
      }
    }
  }
  // 1
  const example1 = {
    tableData: {
      data: [{
          prop1: "No description",
          prop2: "website",
          prop3: "topics",
        },
        {
          prop1: "published",
          prop2: "passive",
          prop3: "Releases",
        },
        {
          prop1: "privade",
          prop2: "",
          prop3: "hideBtn",
        }
      ],
      total: 2,
      page: 1,
      pageSize: 2
    },

    tableMap: [{
        name: "",
        prop: "prop1",
        align: "right"
      },
      {
        name: "",
        prop: "prop2",
        align: "left"
      },
      {
        name: "",
        prop: "prop3"
      },
      {
        name: "",
        width: 300,
        template: {
          btn: [{
            label: "",
            style: "background: green;",
            event: "viewDetails"
          }, {
            label: "",
            style: "background: blue;",
            event: "edit",
            disabled(rowData) {
              return rowData.prop3 == 'hideBtn'
            }
          }]
        }
      }
    ],
    tableConfig: {
      // , 
      hidePage: false, // , false
      hideIndex: false, // , false
      showCheckBox: true // , false
    }
  }


  // 2
  const mixin2 = {
    data: () => {
      return {
        example2
      }
    },
    methods: {
      editEject(data) {
        console.log('is editEject', data.isEdit ? '' : '')
      },
      getBtnLabel(rowData, btn) {
        return (rowData.isEdit ? '' : '');
      }
    },
    computed: {
      vmSelf() {
        return this;
      }
    }
  }
  // 2
  const example2 = {
    tableData: {
      data: [{
          isEdit: true,
        },
        {
          isEdit: false,
        }
      ],
      total: 2,
      page: 1,
      pageSize: 2
    },

    tableMap: [{
        type: "selection",
      }, {
        name: "",
        prop: "isEdit",
      },
      {
        name: "",
        width: 300,
        template: {
          btn: [{
            label: "",
            style: "background: blue; border: none;",
            event: "editEject"
          }]
        }
      }
    ]
  }


  const tableMap = [ //

    {
      name: '',
      prop: 'invoicingDate',
    },
    {
      name: 'sd',
    },
    {
      name: '',
      prop: 'invoiceFee',
    },
    {
      name: '',
      prop: 'invoiceNumber',
    },
    {
      name: '',
      prop: 'ticketHolder',
    },
    {
      name: '',
      prop: 'remark',
    },
    {
      name: '',
      prop: 'authFileName',
      width: 200, //,
      template: { //templat,
        btn: [{
          label: '', //
          style: 'background:#1DB2A0;border:none',
          event: 'invoiceAttachment' //()
        }]
      }
    },
    {
      name: '',
      prop: 'authFileName',
      width: 200, //,
      template: { //templat,
        btn: [{
          label: '', //
          style: 'background:#20335D;border:none',
          event: 'editInvoice' //()
        }]
      }
    }
  ]

  // 3
  const mixin3 = {
    data: () => {
      return {
        example3,
      }
    }
  }
  // 3 data
  const example3 = {
    tableData: {
      data: [{
          comp: 'select'
        },
        {
          comp: 'input'
        }
      ],
      total: 2,
      page: 1,
      pageSize: 2
    },

    tableMap: [{
        name: "",
        prop: "comp",
        // template, prop
        template: {
          input: {
            event: 'change'
          }
        }
      },
      {
        name: "",
        template: {
          select: {
            event: 'change'
          }
        }
      }
    ],
    input: {},
    value: {},
    options: [{
      value: '1',
      label: ''
    }, {
      value: '2',
      label: ''
    }, {
      value: '3',
      label: ''
    }, {
      value: '4',
      label: ''
    }, {
      value: '5',
      label: ''
    }],
  }

  // 4
  const mixin4 = {
    data: () => {
      return {
        example4,
      }
    },
		methods:{
			selectEditHistorical(key,data){
				this.hideTips();
				this.$nextTick(()=>{
					this.$set(this.example4.showKey,key,true)
  				// 
// 					 let dto = {
// 						id:data.id,
// 						status:data.status
// 					 }
// 					 findPayLedgerHistory(dto).then(res => {
// 						this.LedgerHistory = res.data;
// 					 })
					this.example4.LedgerHistory=[{updatedAt:"000000",modifyContent:"999999999999"},{updatedAt:"000000",modifyContent:"999999999999"}]
				});
					
			},
			hideTips() {
				for(let i in this.example4.showKey) {
					this.example4.showKey[i] = false;
				}
			}
		},
		mounted(){
			document.addEventListener('click',this.hideTips);
		},
		destroy(){
			document.removeEventListener('click',this.hideTips);
		}
	}
// 4 data
  const example4 = {
		showKey:{},//
		LedgerHistory:[],
    tableData: {
      data: [{
				modifyNumber:null,
				content:"",
				remark:"",
				id:"1",
				status:"aa"
				},
				{
				modifyNumber:2,
				content:"",
				remark:"",
				id:"2",
				status:"bb"
				}
      ],
      total: 2,
      page: 1,
      pageSize: 2
    },
		tableConfig : { // , 
      hideIndex: true // , false
    },
    tableMap: [
			{
        name: '',
        prop:"index",
        width: 80, // , 
        template: {
          div: {
            event: 'click'
          },
          img:{
            event: 'click'
          }
        }
      },
      {
        name: '',
        prop: 'content',
      },
			{
			  name: '',
			  prop: 'remark',
			},
      {
        name: '',
        prop: 'authFileName',
        width: 200, // , 
        template: { //  templat , 
          btn: [{
            label: '', // 
            style: 'background:#1DB2A0;border:none',
            event: 'invoiceAttachment' // ()
          }]
        }
      }
    ]
  }
  export default {
    components: {
      testMD,
      exp1,
      exp2,
      exp3,
			exp4,
      commonTable,
    },
    mixins: [mixin1, mixin2, mixin3,mixin4]
  };
</script>
<style lang="scss" scoped>
  .wrapper {
    width: calc(100% - 1px);
  }
	.historyInvoice{
    position: absolute;
    left: 75px;
    background: #EAF1FF;
    width: 500px;
    z-index: 9999;
    box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    padding: 20px;
  }
  .historyInvoice .list{
    text-align: left;
    margin-bottom: 20px;
  }
  .historyInvoice .date{
    font-size: 14px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #666666;
    line-height: 10px;
  }
  .historyInvoice .text{
    font-size: 14px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #1A1A1A;
    line-height: 10px;
  }
</style>
<style lang="css" scoped>
  .table_his>>>.el-table__body-wrapper{
    overflow: visible;
  }
  .table_his>>>.el-table{
    overflow: visible;
  }
</style>